<template>
  <div class="home">
    <h1>Список персонажей</h1>
    <div class="functions">
      <my-input
          v-model="searchQuery"
          placeholder="Поиск ..."
          class="input"
      />
      <my-select
          v-model="selectedSort"
          :options="sortOptions"
          class="select"
      />
    </div>
    <my-pagination
        :total-pages="totalPages"
        :current-page="currentPage"
        @page-changed="fetchCards"
        class="pagination"
    />
    <card-list
        v-bind:cards="sortedAndSearchedCards"
        class="list"
    />
  </div>
</template>

<script>
import { ref } from 'vue';
import axios from "axios";
import CardList from "@/components/CardList.vue";
import MySelect from "@/components/UI/MySelect.vue";
import MyInput from "@/components/UI/MyInput.vue";
import MyPagination from "@/components/UI/MyPagination.vue";

export default {
  name: 'HomeView',
  components: {
    MyPagination,
    MyInput,
    MySelect,
    CardList,
  },
  data() {
    return {
      selectedSort: '',
      searchQuery: '',
      cards: [],
      currentPage: 1,
      totalPages: 10,
      loading: true,
      info: {
        count: 826,
        pages: 1,
      },
      sortOptions: [
        { value: 'name', name: 'По имени' },
        { value: 'status', name: 'По статусу' },
      ],
    };
  },
  methods: {
    changePage(pageNumber) {
      this.currentPage = pageNumber;
      this.fetchCards(pageNumber);
    },
    fetchCards(pageNumber) {
      this.currentPage = pageNumber;
      fetch(`https://rickandmortyapi.com/api/character/?page=${pageNumber}`)
          .then(response => response.json())
          .then(data => {
            this.loading = false;
            this.cards = data.results;
            this.totalPages = data.info.pages;
          });
    },
  },
  mounted() {
    this.fetchCards(this.currentPage);
  },
  computed: {
    sortedCards() {
      return [...this.cards].sort((card1, card2) =>
          card1[this.selectedSort]?.localeCompare(card2[this.selectedSort])
      );
    },
    sortedAndSearchedCards() {
      return this.sortedCards.filter(card => card.name.includes(this.searchQuery));
    }
  }
};
</script>

