<template>
  <input
      :value="modelValue"
      @input="updateInput"
      type="text"
      class="input"
  />
</template>

<script>
export default {
  name: 'my-input',
  props: {
    modelValue: [String, Number]
  },
  methods: {
    updateInput(event) {
      this.$emit('update:modelValue', event.target.value)
    }
  }
}
</script>

<style scoped>
.input {
  border: 1px solid turquoise;
  padding: 10px 15px;
  margin: 0;

  font-size: 1vw;
  width: 100%; /* Изменение ширины для адаптивности */
  border-radius: 8px; /* Убраны угловые радиусы, чтобы облегчить адаптивность */
}
@media (max-width: 768px) {
  input{
    height: 45%;
    width: 50%;
  }

}
</style>