<template>
  <div class="select-wrapper">
    <select :value="selectedValue" @change="changeOption" class="select">
      <option disabled value="" class="option font">Выберите из списка</option>
      <option
          class="option"
          v-for="option in options"
          :key="option.value"
          :value="option.value"
      >
        {{ option.name }}
      </option>
    </select>
    <button @click="applyFilter" class="button">Принять</button>
  </div>
</template>

<script>
import { ref, watch } from 'vue';

export default {
  name: 'MySelect',
  props: {
    modelValue: {
      type: String,
    },
    options: {
      type: Array,
      default: () => [],
    },
  },
  setup(props, { emit }) {
    const selectedValue = ref(props.modelValue);

    const changeOption = (event) => {
      selectedValue.value = event.target.value;
    };

    const applyFilter = () => {
      emit('update:modelValue', selectedValue.value);
    };

    watch(() => props.modelValue, (newVal) => {
      selectedValue.value = newVal;
    });

    return {
      selectedValue,
      changeOption,
      applyFilter,
    };
  },
};
</script>

<style scoped>
.select-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end; /* Выравнивание элементов по правому краю */
  justify-content: center;
  width: 100%;
  margin-right: 15px;
  margin-left: 15px;
  font-size: 14px;
}

.select,
.button {
  position: relative;
  width: 100%;
  height: 20px;
  background: linear-gradient(-45deg, #1e0303, #061033);
  color: turquoise;
  text-align: center;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  font-size: 14px;
}
.select {
  border-radius: 8px 8px 0 0 ;
}
.button {
  border-radius: 0 0 8px 8px ;
}

.option {
  background: #061033;
  font-size: 1em;
}


@media (max-width: 768px) {
  .select,
  .button {
    font-size: 10px;
    margin-top: -1px; /* Чтобы убрать зазор между select и button */
    width: 75%;
  }
  .option{
    width: 75%;
  }
}



</style>
